<template>
   <div class="stage1">
      <h2>Уровень 1</h2>
      <p>Coc<span class="spanRed">т</span>авьте ком<span class="spanPurple">б</span>инацию цветов, которая позволит вам перейти на <span class="spanGreen">в</span>торой уровень</p>
      <img src="/img/forest.jpg" alt="forest" class="forest">
      <div class="colorBalls">
        <div class="ball ball1" @click="addStep(1)" 
        :style="{'background': arr[step]}"></div>
        <div class="ball ball2" @click="addStep(2)" 
        :style="{'background': arr[step2]}" ></div>
        <div class="ball ball3" @click="addStep(3)" 
        :style="{'background': arr[step3]}"></div>
      </div>
   </div>
</template>
<script>
export default {
  data() {
    return {
      step:0,
      step2:0,
      step3:0,
      arr: ['green','red','yellow','coral','purple']
    }
  },
  methods: {
    addStep(num) {
       if (num == 1) {
         this.step++
          if (this.step == 5) {
           this.step = 0;
          }
          this.checkStep()
       }
       else if (num == 2) {
         this.step2++
          if (this.step2 == 5) {
           this.step2 = 0;
          }
          this.checkStep()
          
       }
       else if (num == 3) {
         this.step3++
         if (this.step3 == 5) {
           this.step3 = 0;
          }
          this.checkStep()
       }
    },
      checkStep() {
          if (this.step == 1 && this.step2 == 4 && this.step3 == 0) {
            this.$store.state.complete1 = true
          }
       }
  }
}
</script>
<style scoped>
   .stage1 {
     width: 80%;
     margin:0px auto;
     padding: 20px 0;
     text-align: center;
   }
   h2 {
      color:#f1c40f;
      font-family: 'Kelly Slab', cursive;
      text-align:center;
      font-size:35px;
   }
   p {
      color:#fff;
      font-family: 'Roboto',sans-serif;
      text-align:center;
      font-size:16px;
      margin-bottom:20px;
   }
   .spanRed {
     color:red;
   }
   .spanGreen {
     color:green;
   }
   .spanPurple {
     color:#8e44ad;
   }
   .green {
     background-color:green;
   }
   .coral {
      background-color:coral;
   }
   .red {
      background-color:red;
   }
   .purple {
      background-color:#8e44ad;
   }
   .yellow {
     background-color:yellow;
   }
   .colorBalls {
     display: flex;
     justify-content: center;
     padding: 0;
   }

   .ball {
     width: 50px;
     height: 50px;
     border-radius: 50%;
     cursor: pointer;

   }
   .ball1 {
     margin-right:10px;
   }
   .ball3 {
     margin-left:10px;
   }
   .forest {
     height: 300px;
   }
   @media screen and (max-width:780px) {
     .forest {
        width: 100%;
     }
   }
</style>


