<template>
  <div class="stage2">
   <h2>Уровень 2</h2>
    <img src="/img/zombie.png" alt="zombie" class="zombie">
   <div class="fullWord">
     <ul>
       <li v-for="letter of checkArr" :key="letter.id">{{ letter }}</li>
     </ul>
   </div>
   <div class="checkList">
      <div class="form-check form-check-inline">
        <input type="checkbox" class="form-check-input" value="о" v-model="checkArr">
      </div>
      <div class="form-check form-check-inline">
        <input type="checkbox"
        @change="checkCheck($event)" class="form-check-input" value="и" v-model="checkArr">
      </div>
      <div class="form-check form-check-inline">
        <input type="checkbox" class="form-check-input" value="з" v-model="checkArr">
      </div>
      <div class="form-check form-check-inline">
        <input type="checkbox" class="form-check-input" value="б" v-model="checkArr">
      </div>
      <div class="form-check form-check-inline">
        <input type="checkbox" class="form-check-input" value="м" v-model="checkArr">
      </div>
   </div>
  </div>

  
</template>
<script>
export default {
  data() {
    return {
      checkArr: [],
    }
  },
  methods: {
    checkCheck() {
       if (this.checkArr[0] == 'з' && 
       this.checkArr[1] == 'о' && this.checkArr[2] == 'м' 
       && this.checkArr[3] == 'б' && this.checkArr[4] == 'и') {
         this.$store.state.complete2 = true
       }
      
    }
  }
}
</script>
<style scoped>
  .stage2 {
     width: 80%;
     margin:0px auto;
     padding: 20px 0;
     text-align: center;
   }
   h2 {
      color:#f1c40f;
      font-family: 'Kelly Slab', cursive;
      text-align:center;
      font-size:35px;
   }

   .zombie {
     height: 300px;
   }
   .fullWord {
     margin: 20px auto;
     width: 80%;
     min-height: 46px;
     box-sizing: border-box;
   }
   .fullWord  ul{
     list-style: none;
     display: flex;
     justify-content: center;
     padding:0;
     align-items: center;
     min-height:46px;
   }
   .fullWord ul li {
     font-family: 'Kelly Slab', cursive;
     font-size:30px;
     color:#fff;
     transition: .5s;
     margin:0 5px;
   }
    .checkList {
      width: 200px;
      justify-content:space-between;
      margin:20px auto;
      display: flex;
    }

    .form-check {
      margin: 0;
    }

   .checkList input{
      width: 20px;
      height: 20px;
      border:none;
      margin:0;
      padding: 0;
   }

   @media screen and (max-width:460px) {
     .zombie {
       height: 200px;
     }
   }
 
  
</style>


