<template>
  <div class="safe">
    <div class="safeWrap">
      <div class="numberPanel">
        <input type="text" 
        disabled class="numberField" v-model="counter">
        <input type="text" disabled class="numberField" v-model="counter2">
        <input type="text" disabled class="numberField" v-model="counter3">
      </div>
      <div class="btnsAdd">
        <button @click="counter < 9 ? counter++ : false">+</button>
        <button @click="counter2 < 9 ? counter2++ : false">+</button>
        <button @click="counter3 <  9 ? counter3++ : false">+</button>
      </div>
      <div class="btnsSub">
        <button @click="counter > 0 ? counter-- : false">-</button>
        <button @click="counter2 > 0 ? counter2-- : false">-</button>
        <button @click="counter3 > 0 ? counter3-- : false">-</button>
      </div>
      <div class="open">
        <button @click="checkCounters">Open</button>
         <img src="/img/handle.png" alt="handle" class='handle'>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      counter: 0,
      counter2: 0,
      counter3:0,
      victory: false
    }
  },
  methods: {
     checkCounters() {
        if (this.counter == 7 && this.counter2 == 8 && this.counter3 == 6 ) {
           this.$store.state.flag = true;
  
        }
     }
   }
}
</script>
<style scoped>
 
  
   button:active,button:focus {
      outline:none;
   
   }
   .btnsAdd button:active,.btnsAdd button:focus {
      margin: 0;
      padding: 0
   }
   
   .safe {
      width: 300px;
      text-align: center;
      padding: 40px 20px;
      margin:0 auto;
      background:#2c3e50;
      border-radius: 4px;
      min-height: 300px;
      position:relative;
     
   }
     
   .safeWrap {
      width: 90%;
      margin: 0 auto;
      border: 1px solid #000;
      padding:20px 0;
      min-height: 280px;
   }
   .numberPanel {
      display: flex;
      width: 60%;
      margin: 0 auto;
      justify-content: space-around;
   }
   .numberField {
      width: 40px;
      height: 40px;
      border: none;
      text-align: center;
      font-size:30px;

   }

   .btnsAdd {
      margin-top:40px;
      width: 60%;
      margin: 40px auto 0 auto;
      display: flex;
      justify-content: space-around;
   }

   .btnsAdd button {
      cursor:pointer;
      border:none;
      background-color:forestgreen;
      color:#fff;
      border-radius: 4px;
      display: block;
      width: 30px;
      height: 30px;
   }

   .btnsSub {
      margin-top:40px;
      width: 60%;
      margin: 5px auto;
      display: flex;
      justify-content: space-around;
   }

   .btnsSub button {
      cursor:pointer;
      border:none;
      background-color:#2980b9;
      color:#fff;
      border-radius: 4px;
      display: block;
      width: 30px;
      height: 30px;
   }
   
   .open {
      margin:40px auto 0 auto;
      position: relative;
      width: 90%;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   .open button {
      border:none;
      background-color:goldenrod;
      color:#fff;
      font-family: 'Kelly Slab', cursive;
      cursor:pointer;
      text-transform: uppercase;
      display: block;
      height: 40px;
      margin-left:40px;
     
   }
   .handle {
      width: 60px;
      height: 60px;
     
   }
   @media screen and (max-width:360px) {
      .safe {
         width: 100%;
      
     
      }
   }
</style>

