import VueRouter from 'vue-router'
import Home from './pages/Home'
import Info from './pages/Info'
import Errors from './pages/Error'
export default new VueRouter({
  routes: [
    {
      name: 'Home',
      path: '/',
      component: Home
    }, {
      name: 'info',
      path: '/info',
      component: Info
    },
    {
      path: '*',
      component: Errors
    }

  ],
  mode:'history'
})