<template>
  <div class="stage4">
     <h2>Уровень 4</h2>
     <p>Введите в поле пароля секретное слово</p>
      <img src="/img/skull.png" alt="skull" class='skull'>
     <div class="wrapFields">
      <div class="form-group">
          <label for="pass">Пароль:</label>
          <div class='lineInput'>
            <input type="text" class="form-control" id="pass" 
            placeholder="ад" ref="passField">
            <button class="btn btn-success btnField" @click="checkPass">&#8592;</button>
          </div>
          <p class="error" v-show="checkVal">{{ validError }}</p>
      </div>
      <div class="form-group">
          <label for="hint">Подсказка:</label>
          <input type="text" class="form-control ippp" id="hint" 
          @keyup="changeHint">
          <p class="hintWrap">{{ reversedHint }}</p>
      </div>
     </div>

  </div>
  
</template>
<script>
export default {
  data() {
    return {
      hint: '',
      validError: 'Неверный пароль',
      checkVal: false
    }
  },
  computed: {
    reversedHint: function () {
      return this.hint.split('').reverse().join('')
    }
  },
  methods: {
    checkPass() {
      let val = this.$refs.passField.value;
      if (val != 'да') {
         this.checkVal = true;
      } else {
        this.$store.state.complete4 = true
      }
      
    },
    changeHint(event) {
       this.hint = event.target.value
    }
  }
}
</script>
<style scoped>
  .stage4 {
     width: 80%;
     margin:0px auto;
     padding: 20px;
     text-align: center;
   }
   h2 {
      color:#f1c40f;
      font-family: 'Kelly Slab', cursive;
      text-align:center;
      font-size:35px;
   }
   p {
     color:#fff;
     font-family: 'Roboto',sans-serif;
     text-align:center;
     font-size:16px;
     margin-bottom:20px;
   }
   .skull {
     height: 300px;
   }

   .wrapFields {
     width: 40%;
     margin:20px auto 0 auto;
     padding-bottom:40px;
   }

   .wrapFields label {
     color:#fff;
     font-size:16px;
     font-family: 'Roboto',sans-serif;
   }
    .btnField {
      margin-left:5px;
    }
   
   .lineInput {
     display: flex;
     align-items: center;
   }
  .hintWrap {
      box-sizing: border-box;
      min-height:40px;
      overflow: hidden;
      color:#f1c40f;
      padding:8px 0;

    }
    .error {
      box-sizing: border-box;
      min-height:40px;
      overflow: hidden;
      color:#c0392b;
      padding:8px 0;
    }
    @media screen and (max-width:1020px) {
      .wrapFields {
        width: 90%;
      }
      
    }
    @media screen and (max-width:540px) {
      .wrapFields {
        width: 100%;
      } 
      .lineInput {
        display:block; 
        position: relative;
      }
      #pass {
        width: 100%;
        padding-right:60px;
      }
      .btnField {
        position: absolute;
        right: 0;
        top:0;
      }
     
    
    
    }
</style>


